.home {
  display: flex;
  height: 100%;
  width: 100%;

  .homeContent {
    background: linear-gradient(115deg, $color-1 20%, transparent 100%);
    min-height: $minHeight;
    border-radius: $borderRadius;
    position: relative;
    overflow: hidden;
    width: 100%;

    &:after {
      content: "";
      position: absolute;
      height: 100%;
      width: inherit;
      background: url("../../media/K5I1.gif") no-repeat center/cover;
      z-index: -1;
      filter: brightness(30%);
    }

    .content {
      position: absolute;
      top: 20%;
      left: 10%;
      font-family: $font-1;

      h1 {
        font-size: 3rem;
        font-family: $font-3;
        color: $color-3;
        text-transform: uppercase;
        text-shadow: 3px 6px 2px $black;
      }
      h2 {
        font-size: 1.5rem;
        padding: 1.5rem 0 7rem;
        color: $color-3;
        text-shadow: 1px 4px 1px $black;
      }
      .pdf {
        a {
          font-size: 0.8rem;
          background: $color-3;
          padding: 1rem 2rem;
          border-radius: $borderRadius;
          box-shadow: 0 5px 1px rgb(15, 15, 15);
          transition: 0.2s ease;

          &:hover {
            background: $color-1;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 850px) {
  .homeContent {
    margin: 0 !important;
    min-height: 90vh !important;
  }
  .home {
    display: block;
    h1 {
      font-size: 1.5rem !important;
    }
    h2 {
      font-size: 0.8rem !important;
    }
  }
}
@media screen and (max-width: 580px) {
  .homeContent {
    &:after {
      background: url("../../media/K5I1.gif") no-repeat 60% 50% center/cover !important;
    }
  }
}
